.select{position:relative;display:inline-block;font-size:1.125rem;font-family:inherit;box-sizing:border-box;padding:.1rem 2.5rem .2rem .5rem;cursor:pointer;border:1px solid var(--btn-border-color);border-radius:.4rem;background-color:var(--btn-background-color)}.select.active,.select:focus{outline:none;box-shadow:0 0 3px 1px var(--focus-border)}.select::after{content:"▼";position:absolute;display:flex;z-index:1;top:50%;transform:translateY(-50%);right:.6rem;box-sizing:border-box;text-align:center}.value{display:inline-block;width:100%;padding:.3rem 0 .3rem .5rem;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;vertical-align:top}.optList{position:absolute;top:100%;left:0;z-index:2;list-style:none;margin:0;padding:0;box-sizing:border-box;min-width:100%;max-height:10rem;overflow-y:auto;overflow-x:hidden;border:.2rem solid var(--btn-border-color);border-top-width:.1rem;border-radius:0 0 .4rem .4rem;background:var(--select-optList-background-color)}.optList.hidden{max-height:0;visibility:hidden}.option{padding:.2rem .3rem}.selected{background:#0d761d}.highlight{background-color:var(--btn-background-color)}.hiddenInput{position:absolute;inset:0;opacity:0;pointer-events:none}